.general {
  padding-bottom: 2rem;
  border-bottom: 1px solid grey;
  .title {
    font-size: 2rem;
  }
  .form-container {
    position: relative;
    .edit {
      position: absolute;
      top: 0;
      right: 0;
    }
    .form {
      position: absolute;
      z-index: 1000;
      background: rgb(9, 93, 133);
      width: 450px;
      max-width: 95%;
      padding: 1rem;
      top: 40px;
      right: 0;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 1rem 0;
      label {
        font-weight: 600;
        font-size: 1.2rem;
        width: 100%;
        color: white;
      }
      input {
        margin-bottom: 1rem;
        padding: 0.5rem;
        border-radius: 5px;
        border: 1px solid grey;
        width: 100%;
      }

      button {
        width: 100px;
        font-size: 1rem;
        font-weight: 500;
        padding: 0.5rem;
        border-radius: 5px;
        transition: 0.2s ease-in-out;
        background: rgb(219, 232, 255);
        color: #333;
        &:hover {
          filter: brightness(1.1);
        }
      }
    }
  }

  .details {
    text-align: center;
    .name {
      font-size: 2.5rem;
    }
    p {
      font-size: 1.2rem;
    }
  }
}
